@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body{
    /* background-color: rgba(30,30,30,1); */
    max-width: 100vw;
}

.style_schedule__26DKL{
    /* background-color:black;; */
    width:100%;
    height:100%;
    padding-top:1em;
    font-family: 'Montserrat', sans-serif;
}

.style_schedule-container__3IHsP {
    margin: 30px auto;
    padding-bottom:5px;
    background-color:white;
    width: 70%;
    color:orangered; 
    /*box-shadow: 5px 5px 5px 5px rgb(24, 23, 23);*/
}

.style_loader__rAIw2 {
    height: 100px;
    width: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
.style_schedule-header--container__zfF5d {
    width: 100%;
    margin-top:5em;
    background-color: rgb(29, 28, 28);
}

.style_schedule-title__3hDZ8 {
    margin: 40px auto;
    padding-top:1em;
    text-align: center;
    font-family: 'Lemon Milk';
    background: -webkit-linear-gradient(orange,orangered);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size:3.2em;
}

.style_schedule-filter--btn__1dftj {
    position: absolute;
    right: 16%;
    margin-top: 1em;
    font-size:1.25em;
    cursor: pointer;
}

.style_schedule-filter--btn__1dftj:hover {
    color: rgb(179, 31, 31);
}

.style_schedule-tabs--container__2Iga- {
    width: 100%;
    font-size:1.25em !important;
    letter-spacing: 1em !important;
    
}

.style_schedule-noevent--container__2vRnL {
    padding:10px 10px;
    text-align: center;
    color:black;
    font-size: 1rem;
    
}

@media (max-width: 700px){
    .style_schedule-title__3hDZ8 {
        padding-top:.9em;
        margin-bottom:.5em;
        text-align: center;
        font-size:2.5em;
    }
    .style_schedule-container__3IHsP {
        margin: 20px auto;
        width: 80%;
        margin-top:7em;
        padding-bottom:3px;
    }
    .style_schedule-filter--btn__1dftj {
        right: 13%;
        margin-top:.7em;
        font-size:1em;
    }

}

@media (max-width: 500px){
   .style_schedule-container__3IHsP {
        margin: 10px auto;
        width: 90%;
        margin-top:8em;
    }
    .style_schedule-filter--btn__1dftj {
        right: 7%;
        margin-top:.7em;
    }
}
